<template>
  <div class="form-group">
    <div class="showed_select" v-if="options.length > 0">
      <ul>
        <li
          v-for="(index, key) in options"
          :key="key"
          @click="$emit('handleChoose', index, name)"
        >
          {{index[showedKey]}}
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  inheritAttrs: false,
  props: {
    value: [String, Number],
    label: String,
    name: String,
    options: Array,
    showedKey: String
  }
}
</script>
<style>
  .showed_select {
    box-shadow: 0 2px 2px rgba(204, 197, 185, 0.5);
    border: 1px solid #ddd;
    margin-top: 10px;
  }

  .showed_select ul {
    list-style-type: none;
    -webkit-margin-start: 0;
    padding-inline-start: 0px;
    margin-block-start: 0;
    margin-block-end: 0;
  }

  .showed_select ul li {
    cursor: pointer;
    padding: 10px;
  }

  .showed_select ul li:hover {
    background: #0366d6;
    color: #fff;
  }

</style>



